
.testimonials {
    padding-top: 50px;
    padding-bottom: 50px;
    position: relative;
    background-color: var(--color-bg);
  }

  .testimonials .content{
      margin-bottom: 50px;
      display: flex;
      align-items: center;
      justify-content: center;
      flex-direction: column;

  }
  .testimonials h2{
    color: var(--color-primary);
    letter-spacing: 2px;
    margin-bottom: 10px;
  }
  .testimonials p {
      color: #777;
      text-align: center;
      
  }
  
  .testimonials .container {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(300px , 1fr));
    gap: 40px;
  
  }
  @media (max-width:767px){
    .testimonials .container {
      display: grid;
      grid-template-columns: 1fr;
    }
  }
  
  
  .testimonials .box {
    padding: 20px;
    background-color:var(--color-white);
    box-shadow: 0 2px 4px rgb(0 0 0 / 1%);
    border-radius: 6px;
    -webkit-border-radius: 6px;
    -moz-border-radius: 6px;
    -ms-border-radius: 6px;
    -o-border-radius: 6px;
    position: relative;
  }


  .testimonials .box img {
    position: absolute;
    right:-10px;
    top: -20px;
    width: 100px;
    height: 100px;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
    border: 12px solid var(--color-bg);
  }
  @media (max-width:767px){
    .testimonials .box img {
      width: 80px;
      height: 80px;
    }
  }
  
  .testimonials .box h3 {
  margin: 0 0 10px;
  color:var(--color-primary)
  }
  
  .testimonials .box  .title {
    color: black;
    margin-bottom: 10px;
    display: block;
  }
  
  .testimonials .box .rate .filled {
    color:#FFC107;
  
  }
  
  .testimonials .box p {
  color: #767676;
  margin-top: 10px;
  margin-bottom: 0;
  }
  